
.myHeader header {
  width: 100%;
  height: 1.2rem;
  background: #16b13a;
  font-size: 0.4rem;
  text-align: center;
  line-height: 1.2rem;
  color: #fff;
  position: relative;
}
.myHeader header .icon-zuqiu {
  font-size: 0.6rem;
}
.myHeader header .icon-siderbar {
  font-size: 0.5rem;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 2px;
  cursor: pointer;
}

.siderBar .mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 998;
}
.siderBar .author {
  width: 50%;
  background: #2d3044;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  padding-top: 2rem;
  text-align: center;
  font-size: 0.5rem;
  color: #fff;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.siderBar .author.slide-enter,
.siderBar .author.slide-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.siderBar .author .avatar {
  padding: 0.5rem 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.siderBar .author .avatar img {
  width: 100%;
  height: 100%;
}
.siderBar .author .desc {
  font-size: 0.4rem;
  line-height: 1.4;
}
.siderBar .author .icon-guanbi {
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 0.6rem;
  cursor: pointer;
}

.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  line-height: 1rem;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 0.4rem;
  background: #fff;
}
.tab li {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  text-align: center;
  border-bottom: 1px solid #e9e9e9;
}
.tab li.activeChannel {
  border-bottom-color: #16b13a;
  color: #16b13a;
}

.slide {
  min-height: 1px;
  position: relative;
}
.slide .slide-group {
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}
.slide .slide-group .slide-item {
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}
.slide .slide-group .slide-item a {
  display: block;
  width: 100%;
  overflow: hidden;
  text-decoration: none;
  position: relative;
}
.slide .slide-group .slide-item a img {
  display: block;
  width: 100%;
  height: 4.5rem;
}
.slide .slide-group .slide-item a p {
  position: absolute;
  text-align: left;
  bottom: 40px;
  left: 10px;
  z-index: 999;
  font-size: 0.35rem;
  color: #fff;
}
.slide .dots {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 12px;
  -webkit-transform: translateZ(1px);
          transform: translateZ(1px);
  text-align: center;
  font-size: 0;
}
.slide .dots .dot {
  display: inline-block;
  margin: 0 4px;
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 50%;
  background: #ccc;
}
.slide .dots .dot.active {
  width: 0.55rem;
  border-radius: 5px;
  background: #fff;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html,
body {
  font-family: "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
}
@font-face {
  font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,);
  /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'),  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'), url(data:application/x-font-ttf;base64,) format('truetype'),  url(data:image/svg+xml;base64,#iconfont) format('svg');
  /* iOS 4.1- */
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-zuqiu:before {
  content: "\E60A";
}
.icon-fanhui:before {
  content: "\E607";
}
.icon-siderbar:before {
  content: "\E649";
}
.icon-guanbi:before {
  content: "\E64E";
}
.table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  border-top: 1px solid #ddd;
  font-size: 0.3rem;
  background: #e2e2e2;
}
.table th,
.table td {
  height: 0.8rem;
  padding: 2px 3px;
  line-height: 0.8rem;
  text-align: center;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ccc;
}
.table .team {
  text-align: left;
}
.table .team img {
  margin: 0 0.1rem;
  width: 0.55rem;
  height: 0.55rem;
  vertical-align: middle;
}
.table .team_r {
  text-align: right;
}
